import React from 'react';
import { Route, Routes } from 'react-router-dom';
import DashboardLayout from '../../layout/Dashboard';

const ContactList = React.lazy(() => import('./ContactList/Index'));

const ContactRouter = () => (
  <Routes>
    <Route
      path=""
      element={(<DashboardLayout><ContactList /></DashboardLayout>)}
    />
  </Routes>
);

export default ContactRouter;
